import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
import App from './App.vue'
import VueRouter from 'vue-router';
import SuccessList from './components/SuccessList'
import {
    Cell,
    CellGroup,
    Checkbox,
    CheckboxGroup,
    Col,
    DatetimePicker,
    Dialog,
    DropdownItem,
    DropdownMenu,
    Image,
    ImagePreview,
    List,
    Loading,
    NavBar,
    Notify,
    PullRefresh,
    Row,
    Search,
    Toast
} from 'vant';


Vue.use(Notify);
Vue.use(NavBar);
Vue.use(Checkbox).use(CheckboxGroup);
Vue.use(Cell).use(CellGroup).use(ImagePreview).use(Image).use(Toast).use(Loading).use(Dialog).use(DatetimePicker);
Vue.use(Row).use(Col).use(Search).use(DropdownMenu).use(DropdownItem).use(PullRefresh).use(List);
Vue.use(VueRouter);
Vue.use(VueAxios, axios)
Vue.config.productionTip = false
const router = new VueRouter({
    routes: [
        {path: '/', component: SuccessList},
        // {path: '/successList/detail', component: Detail}
    ]
})
new Vue({
    render: function (h) {
        return h(App)
    },
    router
}).$mount('#app')
